import React, { useState } from 'react';

// import Store from '../../store';

const scrollBottom = () => {
	const conversations = document.getElementsByClassName('conversation')[0];
	conversations.scrollTo({
		top: conversations.scrollHeight,
		behavior: 'smooth',
	});
};
export default ({ send }) => {
	// const { store, setContext } = useContext(Store);
	const [msg, setState] = useState('');

  const inputChanged = ({ target: { value } }) => setState(value);

	const _send = (event) => {
		if (event.key === 'Enter') {
      send(msg);
			setState('');
			scrollBottom();
		}
	};

	return (
		<div className="msg-form">
      <input className="form-control" value={msg} onChange={inputChanged} onKeyPress={_send}></input>
    </div>
	)
}
